<template>
    <div class="picker">
        <h5></h5>
        <select v-model="group" class="form-control">
            <option>----</option>
            <option :value="key" v-for="(val, key) in groups">{{val}}</option>
        </select>
        <button @click="confirm" :disabled="disabled" class="btn btn-lg btn-primary"></button>

        <p class="help-text" v-if="user.group.name.indexOf('') > -1">
            2
        </p>
        <p class="help-text" v-else>
            <span v-if="user.group.name==''">21/271/241/27</span>
            <span v-else>21/271/241/27</span>
        </p>
    </div>
</template>
<script>
    export default {
        props: {
            groups: Object,
        },
        data() {
            return {
                group: null
            }
        },
        created() {
            this.group = this.$parent.group
        },
        mounted() {
            $(".i-dot-steps-title").html('')
        },
        computed: {
            disabled() {
                return !this.group
            }
        },
        methods: {
            confirm() {
                this.$cookie.set("RADIOMETER_PURCHASE_GROUP", this.group, {expires: '1Y'})
                this.$parent.group = this.group
                this.$parent.step = "goodsList"
                this.$parent.subStep++
            }
        }
    }
</script>
<style scoped lang="less">
    .picker {
        width: 80%;
        display: block;
        margin: 0 auto;
    }

    h5 {
        text-align: center;
        font-size: 20px;
        font-weight: bold;
        margin: 0 0 20px;
    }

    .help-text {
        margin-top: 10px;
    }
</style>



